import { Link, NavLink } from "react-router-dom";

export const ChatLinks = ({chatId}) => {

  return (
    <div className="chatlinks">
      <NavLink
        to={`/chat/${chatId}`}
        className={({ isActive }) =>
          isActive
            ? "chatlinks__chats chatlinks__subtitle paragraph chatlinks__subtitle_selected"
            : "chatlinks__chats chatlinks__subtitle paragraph"
        }
      >
        <div className="chatlinks__link"></div>
      </NavLink>
      <NavLink
        to={"/direct"}
        className={({ isActive }) =>
          isActive
            ? "chatlinks__chats chatlinks__subtitle paragraph chatlinks__subtitle_selected"
            : "chatlinks__chats chatlinks__subtitle paragraph"
        }
      >
        <div className="chatlinks__link">
          <span className="chatlinks__personal_all"> </span>
          <span className="chatlinks__personal_hidden"></span>
        </div>
      </NavLink>
    </div>
  );
};
